<template>
  <div id="line-work-trend-charts"></div>
</template>

<script>
import * as echarts from "echarts";
import { mapState, mapGetters, mapActions } from "vuex";

export default {
  computed: {
    ...mapState("userWork", ["case"]),
  },
  data() {
    return {
      // 总警力类型数据
      optionsBar: {
        color: ["rgba(0,190,170,1)", "rgba(rgba(66,119,235,1))"],
        grid: {
          top: "8%",
          left: "1%",
          right: "5%",
          bottom: "9%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            data: [
              "00:00",
              "04:00",
              "08:00",
              "12:00",
              "16:00",
              "20:00",
              "24:00",
            ],
            axisTick: {
              show: false,
            },
            axisLabel: {
              color: "#fff",
            },
            axisLine: {
              lineStyle: {
                color: "rgba(255,255,255,0.1)",
              },
            },
          },
        ],
        yAxis: [
          {
            type: "value",
            splitLine: {
              show: true,
              lineStyle: {
                color: ["rgba(255,255,255,0.1)"],
                width: 1,
                type: "solid",
              },
            },
            axisLabel: {
              color: "#fff",
            },
          },
        ],
        series: [
          {
            name: "Email",
            type: "line",
            stack: "Total",
            symbol: "none",
            areaStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(0,190,170,0.1)", // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: "rgba(0,190,170,1)", // 100% 处的颜色
                  },
                ],
                global: false, // 缺省为 false
              },
            },
            emphasis: {
              focus: "series",
            },
            data: [55, 23, 57, 70, 100, 159, 60],
          },
          {
            name: "Union Ads",
            type: "line",
            stack: "Total",
            symbol: "none",
            areaStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(66,119,235,0.1)", // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: "rgba(66,119,235,1)", // 100% 处的颜色
                  },
                ],
                global: false, // 缺省为 false
              },
            },
            emphasis: {
              focus: "series",
            },
            data: [55, 23, 57, 70, 100, 159, 60],
          },
        ],
      },
      workTrendTimer: null,
    };
  },
  mounted() {
    this.initEchart();
    this.workTrendTimer = setInterval(() => {
      this.initEchart();
    }, 3000);
  },
  methods: {
    ...mapActions("userWork", ["asynAddCar"]),
    initEchart() {
      const optionsBar = {
        color: ["rgba(0,190,170,1)", "rgba(rgba(66,119,235,1))"],
        grid: {
          top: "8%",
          left: "1%",
          right: "5%",
          bottom: "9%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            data: [
              "00:00",
              "04:00",
              "08:00",
              "12:00",
              "16:00",
              "20:00",
              "24:00",
            ],
            axisTick: {
              show: false,
            },
            axisLabel: {
              color: "#fff",
            },
            axisLine: {
              lineStyle: {
                color: "rgba(255,255,255,0.1)",
              },
            },
          },
        ],
        yAxis: [
          {
            type: "value",
            splitLine: {
              show: true,
              lineStyle: {
                color: ["rgba(255,255,255,0.1)"],
                width: 1,
                type: "solid",
              },
            },
            axisLabel: {
              color: "#fff",
            },
          },
        ],
        series: [
          {
            name: "Email",
            type: "line",
            stack: "Total",
            symbol: "none",
            areaStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(0,190,170,0.1)", // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: "rgba(0,190,170,1)", // 100% 处的颜色
                  },
                ],
                global: false, // 缺省为 false
              },
            },
            emphasis: {
              focus: "series",
            },
            data: [
              55,
              23,
              57,
              70,
              100,
              159,
              this.case.administrativeCase +
                this.case.criminalCase +
                this.case.otherCase,
            ],
          },
          {
            name: "Union Ads",
            type: "line",
            stack: "Total",
            symbol: "none",
            areaStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(66,119,235,0.1)", // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: "rgba(66,119,235,1)", // 100% 处的颜色
                  },
                ],
                global: false, // 缺省为 false
              },
            },
            emphasis: {
              focus: "series",
            },
            data: [
              55,
              23,
              57,
              70,
              100,
              159,
              this.case.administrativeCase +
                this.case.criminalCase +
                this.case.otherCase,
            ],
          },
        ],
      };
      this.optionsBar = optionsBar;
      const lineTrendChart = document.querySelector("#line-work-trend-charts");
      if (!lineTrendChart) {
        clearInterval(this.workTrendTimer);
        return;
      }
      const myChart = echarts.init(lineTrendChart);
      myChart.setOption(this.optionsBar);
    },
  },
};
</script>

<style lang="scss" scoped>
#line-work-trend-charts {
  width: 100%;
  height: 220px;
}
</style>