<template>
  <div id="pie-warn-charts"></div>
</template>

<script>
import * as echarts from "echarts";

export default {
  data() {
    return {
      // 总警力类型数据
      optionsPie: {
        xAxis: {
          show: false,
        },
        yAxis: {
          type: "category",
          data: ["盗窃", "抢夺", "诈骗", "扒窃", "抢劫"],
          splitLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
          axisLine: {
            show: false,
          },
          axisLabel: {
            color: "#fff",
          },

          // boundaryGap: false,
          // max:20
        },
        barWidth: 7.5,
        grid: [
          {
            left: "10%",
            top: "20",
            right: "10%",
            bottom: "40",
          },
        ],
        series: [
          {
            name: "X",
            type: "bar",
            data: [88, 20, 30, 40, 62],
            label: {
              show: true,
              position: [20, -20],
              valueAnimation: true,
            },
            barCategoryGap: false,
            itemStyle: {
              color: function (params) {
                const colorList = [
                  {
                    type: "linear",
                    x: 0,
                    y: 0,
                    x2: 1,
                    y2: 0,
                    colorStops: [
                      {
                        offset: 0,
                        color: "rgba(0,190,170,0.1)", // 0% 处的颜色
                      },
                      {
                        offset: 1,
                        color: "rgba(0,190,170,1)", // 100% 处的颜色
                      },
                    ],
                    global: false, // 缺省为 false
                  },

                  {
                    type: "linear",
                    x: 0,
                    y: 0,
                    x2: 1,
                    y2: 0,
                    colorStops: [
                      {
                        offset: 0,
                        color: "rgba(255,216,133,0.1)", // 0% 处的颜色
                      },
                      {
                        offset: 1,
                        color: "rgba(255,216,133,1)", // 100% 处的颜色
                      },
                    ],
                    global: false, // 缺省为 false
                  },

                  {
                    type: "linear",
                    x: 0,
                    y: 0,
                    x2: 1,
                    y2: 0,
                    colorStops: [
                      {
                        offset: 0,
                        color: "rgba(255,136,141,0.1)", // 0% 处的颜色
                      },
                      {
                        offset: 1,
                        color: "rgba(255,136,141,1)", // 100% 处的颜色
                      },
                    ],
                    global: false, // 缺省为 false
                  },
                  {
                    type: "linear",
                    x: 0,
                    y: 0,
                    x2: 1,
                    y2: 0,
                    colorStops: [
                      {
                        offset: 0,
                        color: "rgba(126,193,246,0.1)", // 0% 处的颜色
                      },
                      {
                        offset: 1,
                        color: "rgba(126,193,246,1)", // 100% 处的颜色
                      },
                    ],
                    global: false, // 缺省为 false
                  },
                  {
                    type: "linear",
                    x: 0,
                    y: 0,
                    x2: 1,
                    y2: 0,
                    colorStops: [
                      {
                        offset: 0,
                        color: "rgba(141,134,255,0.1)", // 0% 处的颜色
                      },
                      {
                        offset: 1,
                        color: "rgba(141,134,255,1)", // 100% 处的颜色
                      },
                    ],
                    global: false, // 缺省为 false
                  },
                ];
                return colorList[params.dataIndex];
              },
            },

            zlevel: 1,
          },
          {
            name: "X背景",
            type: "bar",
            barGap: "-100%",
            data: [100, 100, 100, 100, 100],

            color: "rgba(0,0,0,0.35)",
          },
        ],
      },
    };
  },
  mounted() {
    this.initEchart();
  },
  methods: {
    initEchart() {
      const pieWarnChart = document.querySelector("#pie-warn-charts");
      const myChart = echarts.init(pieWarnChart);
      myChart.setOption(this.optionsPie);
    },
  },
};
</script>

<style lang="scss" scoped>
#pie-warn-charts {
  width: 100%;
  height: 195px;
}
</style>