<template>
  <div id="bar-work-pie-charts"></div>
</template>

<script>
import * as echarts from "echarts";
import { mapState, mapGetters, mapActions } from "vuex";

export default {
  data() {
    return {
      // 总警力类型数据
      optionsBar: {
        tooltip: {
          show: false, // 去除鼠标hover提示框
        },
        title: [
          {
            text: "有案件有警情",
            left: "0%",
            top: "70%",
            textStyle: {
              fontSize: 12,
              color: "#fff",
              fontWeight: "lighter",
            },
          },
          {
            text: "有案件无警情",
            left: "36%",
            top: "70%",
            textStyle: {
              fontSize: 12,
              color: "#fff",
              fontWeight: "lighter",
            },
          },
          {
            text: "有警情无案件",
            left: "71.5%",
            top: "70%",
            textStyle: {
              fontSize: 12,
              color: "#fff",
              fontWeight: "lighter",
            },
          },
          {
            subtext: "91",
            left: "10.67%",
            top: "75%",
            textAlign: "center",
            subtextStyle: {
              fontSize: 12,
              color: "#00BEAA",
              fontWeight: "lighter",
            },
          },
          {
            subtext: "64",
            left: "50%",
            top: "75%",
            textAlign: "center",
            subtextStyle: {
              fontSize: 12,
              color: "#73B3E6",
              fontWeight: "lighter",
            },
          },
          {
            subtext: "128",
            left: "83.33%",
            top: "75%",
            textAlign: "center",
            subtextStyle: {
              fontSize: 12,
              color: "#4277EB",
              fontWeight: "lighter",
            },
          },
        ],
        graphic: [
          // 第一行
          {
            type: "text",
            left: "center",
            top: "48%",
            style: {
              text: "60%",
              textAlign: "center",
              fill: "#fff",
              fontSize: 14,
            },
          },
          // 第二行
          {
            type: "text",
            left: "26",
            top: "48%",
            style: {
              text: "64%",
              textAlign: "center",
              fill: "#fff",
              fontSize: 14,
            },
          },
          // 第三行
          {
            type: "text",
            right: "30",
            top: "48%",
            style: {
              text: "71%",
              textAlign: "center",
              fill: "#fff",
              fontSize: 14,
            },
          },
        ],
        series: [
          {
            type: "pie",
            name: "名称1",
            radius: ["30", "35"], // 环的宽度
            center: ["13%", "50%"],
            hoverAnimation: false, // 鼠标hover高亮隐藏
            label: {
              normal: {
                position: "inner",
                show: false,
              },
            },
            data: [
              {
                value: 80,
                itemStyle: {
                  normal: {
                    color: "#00BEAA",
                  },
                  label: {
                    show: false, // 去掉指示线
                  },
                  labelLine: {
                    show: false, // 去掉指示线
                  },
                },
              },
              {
                value: 50,
                itemStyle: {
                  normal: {
                    color: "#0B3F5A",
                  },
                  label: {
                    show: false, // 去掉指示线
                  },
                  labelLine: {
                    show: false, // 去掉指示线
                  },
                },
              },
            ],
          },
          {
            type: "pie",
            name: "名称2",
            radius: ["30", "35"], // 环的宽度
            center: ["49%", "50%"],
            hoverAnimation: false, // 鼠标hover高亮隐藏
            label: {
              normal: {
                position: "inner",
                show: false,
              },
            },
            data: [
              {
                value: 80,
                itemStyle: {
                  normal: {
                    color: "#73B3E6",
                  },
                  label: {
                    show: false, // 去掉指示线
                  },
                  labelLine: {
                    show: false, // 去掉指示线
                  },
                },
              },
              {
                value: 70,
                itemStyle: {
                  normal: {
                    color: "#0B3F5A",
                  },
                  label: {
                    show: false, // 去掉指示线
                  },
                  labelLine: {
                    show: false, // 去掉指示线
                  },
                },
              },
            ],
          },
          {
            type: "pie",
            name: "名称3",
            radius: ["30", "35"], // 环的宽度
            center: ["85%", "50%"],
            hoverAnimation: false, // 鼠标hover高亮隐藏
            label: {
              normal: {
                position: "inner",
                show: false,
              },
            },
            data: [
              {
                value: 80,
                itemStyle: {
                  normal: {
                    color: "#4277EB",
                  },
                  label: {
                    show: false, // 去掉指示线
                  },
                  labelLine: {
                    show: false, // 去掉指示线
                  },
                },
              },
              {
                value: 50,
                itemStyle: {
                  normal: {
                    color: "#0B3F5A",
                  },
                  label: {
                    show: false, // 去掉指示线
                  },
                  labelLine: {
                    show: false, // 去掉指示线
                  },
                },
              },
            ],
          },
        ],
      },
      workPieTimer: null,
    };
  },
  computed: {
    ...mapState("userWork", ["case", "caseType"]),
    ...mapGetters("userWork", ["totalCase"]),
  },
  mounted() {
    this.initEchart();
    this.workPieTimer = setInterval(() => {
      this.initEchart();
    }, 3000);
  },
  methods: {
    initEchart() {
      this.optionsBar = {
        tooltip: {
          show: false, // 去除鼠标hover提示框
        },
        title: [
          {
            text: "有案件有警情",
            left: "0%",
            top: "70%",
            textStyle: {
              fontSize: 12,
              color: "#fff",
              fontWeight: "lighter",
            },
          },
          {
            text: "有案件无警情",
            left: "36%",
            top: "70%",
            textStyle: {
              fontSize: 12,
              color: "#fff",
              fontWeight: "lighter",
            },
          },
          {
            text: "有警情无案件",
            left: "71.5%",
            top: "70%",
            textStyle: {
              fontSize: 12,
              color: "#fff",
              fontWeight: "lighter",
            },
          },
          {
            subtext: this.case.administrativeCase,
            left: "10.67%",
            top: "75%",
            textAlign: "center",
            subtextStyle: {
              fontSize: 12,
              color: "#00BEAA",
              fontWeight: "lighter",
            },
          },
          {
            subtext: this.case.otherCase,
            left: "50%",
            top: "75%",
            textAlign: "center",
            subtextStyle: {
              fontSize: 12,
              color: "#73B3E6",
              fontWeight: "lighter",
            },
          },
          {
            subtext: this.case.criminalCase,
            left: "83.33%",
            top: "75%",
            textAlign: "center",
            subtextStyle: {
              fontSize: 12,
              color: "#4277EB",
              fontWeight: "lighter",
            },
          },
        ],
        graphic: [
          // 第一行
          {
            type: "text",
            left: "center",
            top: "48%",
            style: {
              text: `${Math.round(
                (this.case.otherCase / this.totalCase) * 100
              )}%`,
              textAlign: "center",
              fill: "#fff",
              fontSize: 14,
            },
          },
          // 第二行
          {
            type: "text",
            left: "26",
            top: "48%",
            style: {
              text: `${Math.round(
                (this.case.administrativeCase / this.totalCase) * 100
              )}%`,
              textAlign: "center",
              fill: "#fff",
              fontSize: 14,
            },
          },
          // 第三行
          {
            type: "text",
            right: "30",
            top: "48%",
            style: {
              text: `${Math.round(
                (this.case.criminalCase / this.totalCase) * 100
              )}%`,
              textAlign: "center",
              fill: "#fff",
              fontSize: 14,
            },
          },
        ],
        series: [
          {
            type: "pie",
            name: "名称1",
            radius: ["30", "35"], // 环的宽度
            center: ["13%", "50%"],
            hoverAnimation: false, // 鼠标hover高亮隐藏
            label: {
              normal: {
                position: "inner",
                show: false,
              },
            },
            data: [
              {
                value: Math.round((this.case.otherCase / this.totalCase) * 100),
                itemStyle: {
                  normal: {
                    color: "#00BEAA",
                  },
                  label: {
                    show: false, // 去掉指示线
                  },
                  labelLine: {
                    show: false, // 去掉指示线
                  },
                },
              },
              {
                value: 50,
                itemStyle: {
                  normal: {
                    color: "#0B3F5A",
                  },
                  label: {
                    show: false, // 去掉指示线
                  },
                  labelLine: {
                    show: false, // 去掉指示线
                  },
                },
              },
            ],
          },
          {
            type: "pie",
            name: "名称2",
            radius: ["30", "35"], // 环的宽度
            center: ["49%", "50%"],
            hoverAnimation: false, // 鼠标hover高亮隐藏
            label: {
              normal: {
                position: "inner",
                show: false,
              },
            },
            data: [
              {
                value: Math.round(
                  (this.case.administrativeCase / this.totalCase) * 100
                ),
                itemStyle: {
                  normal: {
                    color: "#73B3E6",
                  },
                  label: {
                    show: false, // 去掉指示线
                  },
                  labelLine: {
                    show: false, // 去掉指示线
                  },
                },
              },
              {
                value: 70,
                itemStyle: {
                  normal: {
                    color: "#0B3F5A",
                  },
                  label: {
                    show: false, // 去掉指示线
                  },
                  labelLine: {
                    show: false, // 去掉指示线
                  },
                },
              },
            ],
          },
          {
            type: "pie",
            name: "名称3",
            radius: ["30", "35"], // 环的宽度
            center: ["85%", "50%"],
            hoverAnimation: false, // 鼠标hover高亮隐藏
            label: {
              normal: {
                position: "inner",
                show: false,
              },
            },
            data: [
              {
                value: Math.round(
                  (this.case.criminalCase / this.totalCase) * 100
                ),
                itemStyle: {
                  normal: {
                    color: "#4277EB",
                  },
                  label: {
                    show: false, // 去掉指示线
                  },
                  labelLine: {
                    show: false, // 去掉指示线
                  },
                },
              },
              {
                value: 50,
                itemStyle: {
                  normal: {
                    color: "#0B3F5A",
                  },
                  label: {
                    show: false, // 去掉指示线
                  },
                  labelLine: {
                    show: false, // 去掉指示线
                  },
                },
              },
            ],
          },
        ],
      };
      const barServiceTypeChart = document.querySelector(
        "#bar-work-pie-charts"
      );
      if (barServiceTypeChart) {
        const myChart = echarts.init(barServiceTypeChart);
        myChart.setOption(this.optionsBar);
      } else {
        clearInterval(this.workPieTimer);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
#bar-work-pie-charts {
  width: 100%;
  height: 220px;
}
</style>