<template>
  <div id="bar-work-number-charts"></div>
</template>

<script>
import * as echarts from "echarts";
import { mapState, mapGetters, mapActions } from "vuex";

export default {
  data() {
    return {
      // 总警力类型数据
      optionsBar: {
        grid: {
          left: "2%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        textStyle: {
          color: "red",
          fontSize: 12,
          fontWeight: "bolder",
        },
        xAxis: {
          type: "category",
          data: ["成华区", "高新区", "锦江区", "金牛区", "青羊区", "武侯区"],
          axisTick: {
            show: false,
          },
          axisLabel: {
            color: "#fff",
            interval: 0,
            fontWeight: "lighter",
          },
          axisLine: {
            lineStyle: {
              color: "rgba(255,255,255,0.1)",
            },
          },
        },
        yAxis: {
          type: "value",
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255,255,255,0.1)"],
              width: 1,
              type: "solid",
            },
          },
          axisLabel: {
            color: "#fff",
          },
        },
        barWidth: 16,
        series: [
          {
            name: "Direct",
            type: "bar",
            stack: "total",
            emphasis: {
              focus: "series",
            },

            data: [320, 302, 301, 334, 390, 330, 320],
            itemStyle: {
              normal: {
                color: "#3F7BFF",
              },
            },
          },
          {
            name: "Mail Ad",
            type: "bar",
            stack: "total",
            emphasis: {
              focus: "series",
            },
            data: [120, 132, 101, 134, 90, 230, 210],
            itemStyle: {
              normal: {
                color: "#FFD885",
              },
            },
          },
          {
            name: "Affiliate Ad",
            type: "bar",
            stack: "total",
            emphasis: {
              focus: "series",
            },
            label: {
              show: true,
              position: "top",
            },
            itemStyle: {
              normal: {
                color: "#FF888D",
              },
            },

            data: [220, 182, 191, 234, 290, 330, 310],
          },
        ],
      },
      barWorkTimer: null,
      totalCaseArea: {},
    };
  },
  mounted() {
    this.totalCaseArea = this.caseArea;
    this.initEchart();
    this.barWorkTimer = setInterval(() => {
      this.asynAddArea();
      this.initEchart();
    }, 3000);
  },
  computed: {
    ...mapState("userWork", ["caseArea"]),
  },
  methods: {
    ...mapActions("userWork", ["asynAddArea"]),

    initEchart() {
      const optionsBar = {
        grid: {
          left: "2%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        textStyle: {
          color: "red",
          fontSize: 12,
          fontWeight: "bolder",
        },
        xAxis: {
          type: "category",
          data: ["成华区", "高新区", "锦江区", "金牛区", "青羊区", "武侯区"],
          axisTick: {
            show: false,
          },
          axisLabel: {
            color: "#fff",
            interval: 0,
            fontWeight: "lighter",
          },
          axisLine: {
            lineStyle: {
              color: "rgba(255,255,255,0.1)",
            },
          },
        },
        yAxis: {
          type: "value",
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255,255,255,0.1)"],
              width: 1,
              type: "solid",
            },
          },
          axisLabel: {
            color: "#fff",
          },
        },
        barWidth: 16,
        series: [
          {
            name: "Direct",
            type: "bar",
            stack: "total",
            emphasis: {
              focus: "series",
            },

            data: [
              this.totalCaseArea.jn,
              this.totalCaseArea.qy,
              this.totalCaseArea.wh,
              this.totalCaseArea.gx,
              this.totalCaseArea.jj,
              this.totalCaseArea.ch,
            ],
            itemStyle: {
              normal: {
                color: "#3F7BFF",
              },
            },
          },
          {
            name: "Mail Ad",
            type: "bar",
            stack: "total",
            emphasis: {
              focus: "series",
            },
            data: [
              this.totalCaseArea.jn,
              this.totalCaseArea.qy,
              this.totalCaseArea.wh,
              this.totalCaseArea.gx,
              this.totalCaseArea.jj,
              this.totalCaseArea.ch,
            ],
            itemStyle: {
              normal: {
                color: "#FFD885",
              },
            },
          },
          {
            name: "Affiliate Ad",
            type: "bar",
            stack: "total",
            emphasis: {
              focus: "series",
            },
            label: {
              show: true,
              position: "top",
            },
            itemStyle: {
              normal: {
                color: "#FF888D",
              },
            },

            data: [
              this.totalCaseArea.jn,
              this.totalCaseArea.qy,
              this.totalCaseArea.wh,
              this.totalCaseArea.gx,
              this.totalCaseArea.jj,
              this.totalCaseArea.ch,
            ],
          },
        ],
      };
      this.optionsBar = optionsBar;
      const barTypeChart = document.querySelector("#bar-work-number-charts");
      if (!barTypeChart) {
        clearInterval(this.barServiceTimer);
        return;
      }
      const myChart = echarts.init(barTypeChart);
      myChart.setOption(this.optionsBar);
    },
  },
};
</script>

<style lang="scss" scoped>
#bar-work-number-charts {
  width: 100%;
  height: 220px;
}
</style>