<template>
  <div id="pie-type-work-charts"></div>
</template>

<script>
import * as echarts from "echarts";

export default {
  props: {
    totalCase: { type: Number, default: 524 },
  },
  data() {
    let percent = 0.5; //percent
    let current = 270 * percent; // 当前用量 = all * percent
    let all = 360; //彩色部分总大小，自定义，和最下方bottom的值一起决定环形图的角度
    return {
      // 总警力类型数据
      optionsPie: {
        title: [
          {
            subtext: "案件总数",
            left: "41%",
            top: "70%",
            textStyle: {
              fontSize: 12,
              color: "#ccc",
              fontWeight: "lighter",
            },
          },
        ],
        series: [
          {
            type: "pie",
            center: ["50%", "68%"],
            radius: ["60%", "120%"],
            startAngle: 188, //起始角度，根据实际需要调节
            label: {
              show: false,
            },
            data: [
              {
                name: "用量",
                value: current,
                label: {
                  show: true,
                  position: "center",
                  formatter: `${this.totalCase}`,
                  textStyle: {
                    baseline: false,
                    fontSize: 25,
                    color: "rgba(255, 255, 255)",
                    fontWeight: "lighter",
                  },
                },
                labelLine: {
                  show: false,
                },
                itemStyle: {
                  color: {
                    type: "linear",
                    x: 0,
                    y: 0,
                    x2: 1,
                    y2: 1,
                    colorStops: [
                      {
                        offset: 0,
                        color: "rgba(66,119,235, 0.1)", // 0% 处的颜色
                      },
                      {
                        offset: 1,
                        color: "rgba(66,119,235, 1)", // 100% 处的颜色
                      },
                    ],
                    global: false, // 缺省为 false
                  },
                },
              },
              {
                name: "rest", // 实际显示部分是总量-用量
                value: all - current,
                itemStyle: {
                  color: {
                    type: "linear",
                    x: 0,
                    y: 0,
                    x2: 1,
                    y2: 1,
                    colorStops: [
                      {
                        offset: 0,
                        color: "rgba(255,135,140, 0.1)", // 0% 处的颜色
                      },
                      {
                        offset: 1,
                        color: "rgba(255,135,140, 1)", // 100% 处的颜色
                      },
                    ],
                    global: false, // 缺省为 false
                  },
                },
              },
              {
                name: "rest", // 实际显示部分是总量-用量
                value: 80,
                itemStyle: {
                  color: {
                    type: "linear",
                    x: 0,
                    y: 0,
                    x2: 1,
                    y2: 1,
                    colorStops: [
                      {
                        offset: 0,
                        color: "rgba(238,178,47, 0.1)", // 0% 处的颜色
                      },
                      {
                        offset: 1,
                        color: "rgba(238,178,47, 1)", // 100% 处的颜色
                      },
                    ],
                    global: false, // 缺省为 false
                  },
                },
              },
              {
                name: "bottom",
                value: 360, //底部透明部分的颜色，看实际情况，如果需要的是半圆图，这个透明部分的value值就变成和all相同的值，以此类推，可以自己调节value的大小
                itemStyle: {
                  color: "transparent",
                },
              },
            ],
          },
        ],
      },
    };
  },
  mounted() {
    this.initEchart();
  },
  methods: {
    initEchart() {
      const pieTypeChart = document.querySelector("#pie-type-work-charts");
      const myChart = echarts.init(pieTypeChart);
      myChart.setOption(this.optionsPie);
    },
  },
};
</script>

<style lang="scss" scoped>
#pie-type-work-charts {
  width: 100%;
  height: 165px;
}
</style>